<template>
	<div>
		<h1 class="title">
			<router-link to="/phone">
				<ik-icon type="home" 	/>
			</router-link>
			Icon-图标
		</h1>
		<div>
			<h2>常用</h2>
			<div class="box">
				<div v-for="item in basic" :key="item.name">
					<ik-icon :type="item.name" 	:color="item.color" size="32" />
					<p>{{item.name}}</p>
				</div>
			</div>
		</div>
		<div>
			<h2>所有</h2>
			<div class="box">
				<div v-for="item in iconName" :key="item.name">
					<ik-icon :type="item.name" 	size="32"/>
					<p>{{item.name}}</p>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import iconName from './iconName.js'
export default {
	data () {
		return {
			iconName,
			basic: [
				{name: 'checkmark', color: '#09bb07'},
				{name: 'checkmark-circled', color: '#09bb07'},
				{name: 'information', color: '#10aeff'},
				{name: 'information-circled', color: '#10aeff'},
				{name: 'alert', color: '#ff9900'},
				{name: 'alert-circled', color: '#ff9900'},
				{name: 'help', color: '#ff0000'},
				{name: 'help-circled', color: '#ff0000'},
				{name: 'close', color: '#ed3f14'},
				{name: 'close-circled', color: '#ed3f14'},
			]
		}
	}
}
</script>

<style lang="less" scoped>
.title {
	font-size: 20px;
}

.box {
	display: flex;
	flex-wrap: wrap;
	div {
		width: 50%;
		padding: 20px;
		text-align: center;
	}
}
</style>




